<template>
  <div v-loading="mainPage.loading">
    <div style="margin-top: 1%;" v-for="(z_item,z_index) in mainPage.zone" :key="z_index">
      <div align="center">
        <el-tag size="large" :type="getType(z_item)">{{ z_item }} 区</el-tag>
      </div>

      <div align="center" style="margin-top: 1%">
            <span v-for="(q_item,q_index) in mainPage.quickResourceList" :key="q_index">
                <span v-if="q_item.zone === z_item">
                    <a style="text-decoration:none;" :href="q_item.url" target="_blank">
                        <span class="z_item_span">{{ q_item.name }}</span>
                    </a>
                </span>
            </span>
      </div>

    </div>
    <div style="margin-bottom: 50px;"></div>
  </div>
</template>

<script>
export default {
  name: "QuickResource",
  data: () => ({
    mainPage: {
      loading: false,
      quickResourceList: [],//资源列表
      zone: [],//专区（A区、B区）
    }
  }),
  mounted() {
    this.initQuickResourceList();
  },
  methods: {
    getType(zone) {
      switch (zone) {
        case 'A':
          return 'success';
        case 'B':
          return '';
        case 'C':
          return 'info';
        case 'D':
          return 'warning';
        case 'E':
          return 'danger';
        default:
          return 'info';

      }
    },
    /**
     * 初始化资源列表
     */
    initQuickResourceList() {
      this.mainPage.zone = true;
      const  data = require('./quickResource.json');
      this.mainPage.quickResourceList = data.quickResourceList;
      this.mainPage.zone = data.quickResourceZone;
      this.mainPage.loading = false;
    }
  }
}
</script>

<style scoped>
.z_item_span {
  font-size: 15px;

  font-family: "Times New Roman";

  color: rgba(144, 47, 203, 1);

  /*边框圆润度*/
  border-radius: 5px;
  /*背景内边距*/
  background-clip: padding-box;
  /*边框*/
  border: 1px solid #eaeaea;
  /*阴影*/
  box-shadow: 0 0 10px #cac6c6;

  /*内边距*/
  padding: 0.3% 0.3% 0.3%;

  /*背景颜色*/
  background: rgba(199, 233, 243, 0.1);

  margin: 1% 1% 1% 1%;

  max-width: 20%;
}
</style>